@import '../utils/colors.scss';
@import '../utils/font.scss';
@import '../utils/hovers.scss';


@mixin default-button {
  border: 0;
  border-radius: 12px;
  padding: 10px;
  color: $default-color-text;
  background-color: $default-button-color;
  
  &:hover {
    @include default-button-hover;
  }
}

@mixin login-button {
  border: 0;
  border-radius: 10px;
  padding: 10px 30px 10px 30px;

  @include default-hover;

  @include tablets {
    background-color: rgb(175, 170, 170);
  }
  
}

@mixin starter-button {
  @include starter-button-text;
  margin: 40px;
  padding: 40px 190px;
  border: solid white 1px;
  border-radius: 60px;
  background-color: rgba(0, 125, 215, 0);
  

  &:hover {
    background-color: (#cc2b5e);
  }

  @include desktop {
    padding: 30px 143px;
  }

  @include laptop {
    padding: 24px 114px;
  }

  @include tablets {
    padding: 30px 143px;
  }

  @include phones {
    padding: 23px 100px;
  }
}